@import "../../variables";
@import url("https://fonts.googleapis.com/css2?family=Source+Sans+Pro&display=swap");

.work_container {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #2f80ed;
}

.holder {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  bottom: 0;
  left: 0;
  margin-top: 5%;
  width: auto;
  background-color: black;
  width: 70%;
  height: auto;
  color: white;
  padding-bottom: 20px;
  right: 0;
  @media only screen and (max-width: 600px) {
    width: 90%;
  }
}
img {
  width: 100px;
  height: 100px;
  float: right;
}
.section_title {
  font-size: 40px;
  margin: 20px 0 20px 0;
  border-bottom: 2px solid white;
  text-align: center;
}
